@import url(https://fonts.googleapis.com/css2?family=Philosopher&display=swap);
header {
  overflow: hidden;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
  font-family: 'Philosopher';
  color: white;
  text-shadow: 2px 2px 4px #000000;
  cursor: pointer;
  width: 100%;
}

.bar{
  /* height: 50px; */
  width : 10rem;
  background-color: rgb(196, 196, 196);
  margin-left: .2rem;
  margin-right: .2rem;
  margin-top: auto;
  font-size: 1rem;
}

.bar-color{
  background-color: rgb(196, 196, 196);
}

.active-bar{
  background-color: rgb(153, 223, 173) !important;
}

.sorted{
  background-color: rgb(78, 214, 117) !important;
}

.check{
  background-color: rgb(221, 223, 173) !important;
}
* {box-sizing: border-box;}

body { 
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.bar-box{
    /* margin: auto; */ 
    /* border-style: solid; */
    /* border-radius: 1rem; */
    border-width: .5rem;
    border-color: white;
    margin-bottom: 64px;/* footer se chipnewala area dikhana ke liye*/
}
.bar-box .horizintal-bars{
    width: 100%;
    height: 520px;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    display: -webkit-inline-flex;
    display: inline-flex;
    border-style: solid;
    border-width: .2rem;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    overflow: auto;
}
.property-box {
    width: auto;
    border-color: white;
    height: 560px;
    border-style: solid;
    border-width: .2rem;
}
.property-box button {
    height: 2.5rem;
    font-size: 1rem;
    padding-top: 5px;
}
.property-box input {
    width : 5rem;
    height: 2.5rem;
    vertical-align: center;
}

.property-box select{
    height: 2.5rem;
}

@media screen and (max-width: 768px) {
    .property-box{
        height: auto;
    }
}

.message{
    width: 100%;
    color: rgb(31, 35, 33);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.bar-container{
    border-color: azure;
}

.colorbox{
    height: 15px;
    width: 15px;
}
footer {
  text-align: center;
  background-color: rgb(233, 232, 232);
  color: black;
  width: 100%;
  /* position: fixed; */
  left: 0;
  bottom: 0;
  height: 100px;
}

footer h5 img {
  height : 30px;
}

footer a {
  padding-left: 10px;
}
